import React from "react";
import "./App.css";
import upload_video from "./videos/1-upload-video.mp4";
import analysis_video from "./videos/2-analysis-result.mp4";
import search_scene from "./videos/3-search-scene.mp4";
import insert_product from "./videos/4-insert-product.mp4";
import ads_display from "./videos/5-ads-display.mp4";
import product from "./videos/product.mp4";
import maskrcnn from "./images/image.png";
import "antd/dist/antd.css";


class DemoPage extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
            tabs: [
                {tabName: "Upload Video", id: 1},
                {tabName: "Analyse Video", id: 2},
                {tabName: "Search Scene", id: 3},
                {tabName: "Insert Product", id: 4},
                {tabName: "Display Ads", id: 5},
                {tabName: "Product App", id: 6}
            ],
            currentIndex: 1,
        };
    }

    componentDidMount() {

    }

    tabChoiced = (id) => {
        this.setState({
            currentIndex: id
        });
    }

    render() {
        var _this = this;
        var isBox1Show = this.state.currentIndex === 1 ? 'block' : 'none';
        var isBox2Show = this.state.currentIndex === 2 ? 'block' : 'none';
        var isBox3Show = this.state.currentIndex === 3 ? 'block' : 'none';
        var isBox4Show = this.state.currentIndex === 4 ? 'block' : 'none';
        var isBox5Show = this.state.currentIndex === 5 ? 'block' : 'none';
        var isBox6Show = this.state.currentIndex === 6 ? 'block' : 'none';


        var tabList = this.state.tabs.map(function (res, index) {
            var tabStyle = res.id === this.state.currentIndex ? 'subCtrl active' : 'subCtrl';
            return <ul key={index} onClick={this.tabChoiced.bind(_this, res.id)} className={tabStyle}>{res.tabName}</ul>
        }.bind(_this));
        return (
            <div className="App">
                <h1 align="center" style={{marginTop: "15px", fontSize: 25}}>
                    Hysia: Serving DNN-Based Video-to-Retail Applications in Cloud
                </h1>
                <p style={{marginTop: "-10px"}}>
                    Huaizheng Zhang, Yuanming Li, Qiming Ai, Yong Luo,
                    Yonggang Wen, Yichao Jin, Nguyen Binh Duong Ta
                </p>
                <p style={{marginTop: "-8px"}}>
                    Nanyang Technological University, Indeed Inc., Singapore Management University
                </p>
                <p align="center" style={{marginTop: "15px"}}>
                    <a href="https://github.com/cap-ntu/Video-to-Online-Platform">Code</a>{" "}
                    •&nbsp;
                    <a href="https://github.com/cap-ntu/Video-to-Online-Platform">Paper</a>{" "}
                    •&nbsp;
                    <a href="https://github.com/cap-ntu/Video-to-Online-Platform">
                        Tutorial
                    </a>{" "}
                    •&nbsp;
                    <a href="https://github.com/cap-ntu/Video-to-Online-Platform">Slides</a>{" "}
                    •&nbsp;
                    <a href="https://github.com/cap-ntu/Video-to-Online-Platform">
                        Citation
                    </a>{" "}
                    •&nbsp;
                    <a href="https://github.com/cap-ntu/Video-to-Online-Platform">News</a>
                </p>

                <h1 align="left" style={{marginTop: "15px", fontSize: 25}}>
                    Abstract
                </h1>

                <div className="Abstract">
                    <p align="left" style={{wordBreak: "normal"}}>
                        Coming soon.
                    </p>
                </div>

                <h1 align="left" style={{marginTop: "15px", fontSize: 25}}>
                    Demo
                </h1>

                <div className="listWrap">
                    <img src={maskrcnn} width="75%" alt="..." />
                    <div className="subNavWrap">
                        {tabList}
                    </div>
                    <div className="newsList">
                        <div style={{"display": isBox1Show}}>
                            <video src={upload_video} width="75%" controls="controls"/>
                        </div>
                        <div style={{"display": isBox2Show}}>
                            <video src={analysis_video} width="75%" controls="controls"/>
                        </div>
                        <div style={{"display": isBox3Show}}>
                            <video src={search_scene} width="75%" controls="controls"/>
                        </div>
                        <div style={{"display": isBox4Show}}>
                            <video src={insert_product} width="75%" controls="controls"/>
                        </div>
                        <div style={{"display": isBox5Show}}>
                            <video src={ads_display} width="75%" controls="controls"/>
                        </div>
                        <div style={{"display": isBox6Show}}>
                            <video src={product} width="75%" controls="controls"/>
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}

export default DemoPage;

